import {
  Home,
  Ranking,
  About,
  NearBy,
  Notes,
  MyProblems,
  Notices
} from "./actionTypes";

export const setHome = () => {
  return {
    type: Home
  };
};

export const setRanking = () => {
  return {
    type: Ranking
  };
};

export const setAbout = () => {
  return {
    type: About
  };
};

export const setNearBy = () => {
  return {
    type: NearBy
  };
};

export const setMyProblems = () => {
  return {
    type: MyProblems
  };
};
export const setNotes = () => {
  return {
    type: Notes
  };
};
export const setNotices = () => {
  return {
    type: Notices
  };
};
