import React, { useState } from "react";
import moment from "moment";
import { connect } from "react-redux";
import {
  IconButton,
  CircularProgress,
  TextField,
  Container,
} from "@material-ui/core";
import PropTypes from "prop-types";
import { Button, ModalHeader, Comment, CommentAvatar } from "semantic-ui-react";
import url from "../../../../strings/url";

import {
  UpVote,
  RemoveUpVote,
  DownVote,
  RemoveDownVote,
  getComments,
  postComment,
} from "../../../../ApiRequests/request";
import {
  ArrowUpward,
  ArrowDownward,
  Delete,
  CommentOutlined,
} from "@material-ui/icons";
import {
  Image,
  Card,
  Divider,
  PlaceholderImage,
  Placeholder,
  Modal,
} from "semantic-ui-react";
import { blue, red } from "@material-ui/core/colors";
import { Result } from "antd";

const fontStyle = {
  fontFamily: "poppins",
  fontSize: 12,
};
const fontStyle1 = {
  fontFamily: "poppins",
  fontSize: 15,
};

const Problem = (props) => {
  const [upVotes, setUpVotes] = useState(props.UpVotes);
  const [downVotes, setDownVotes] = useState(props.DownVotes);
  const [isUpVoted, setUpVote] = useState(props.UpVotedByMe);
  const [isDownVoted, setDownVote] = useState(props.DownVotedByMe);
  const [commentSection, setCommentSection] = useState(false);
  const [commentDescription, setCommentDescription] = useState("");
  const [commentSectionLoading, setCommentSectionLoading] = useState(false);
  const [commentsFetching, setCommentsFetching] = useState(false);
  const [comments, setComments] = useState([]);
  const handleGetComments = async (token, id) => {
    try {
      setCommentsFetching(true);
      const res = await getComments(
        props.id,
        localStorage.getItem("AnonymityToken#123")
      );
      if (res.status === 200) {
        const rer = await res.json();
        setComments(rer);
        setCommentsFetching(false);
      }
    } catch (e) {
      console.log(e.message);
      setCommentsFetching(false);
    }
  };
  const HandleUpVote = () => {
    if (isDownVoted) {
      setDownVote(false);
      setDownVotes((state) => {
        return state - 1;
      });
      setUpVotes((state) => {
        return state + 1;
      });
      setUpVote((state) => {
        return true;
      });
      UpVote(props.id, localStorage.getItem("AnonymityToken#123")).then(
        (res) => {
          RemoveDownVote(props.id, localStorage.getItem("AnonymityToken#123"));
        }
      );
    } else if (!isUpVoted) {
      setUpVotes((state) => {
        return state + 1;
      });
      setUpVote((state) => {
        return true;
      });
      UpVote(props.id, localStorage.getItem("AnonymityToken#123"));
    } else if (isUpVoted) {
      setUpVotes((state) => {
        return state - 1;
      });
      setUpVote((state) => {
        return false;
      });
      RemoveUpVote(props.id, localStorage.getItem("AnonymityToken#123"));
    }
  };
  const handleDownVote = () => {
    if (isUpVoted) {
      setUpVote(false);
      setUpVotes((state) => {
        return state - 1;
      });
      setDownVotes((state) => {
        return state + 1;
      });
      setDownVote(true);
      DownVote(props.id, localStorage.getItem("AnonymityToken#123")).then(
        (res) => {
          return RemoveUpVote(
            props.id,
            localStorage.getItem("AnonymityToken#123")
          );
        }
      );
    } else if (!isDownVoted) {
      setDownVotes((state) => {
        return state + 1;
      });
      setDownVote(true);
      DownVote(props.id, localStorage.getItem("AnonymityToken#123"));
    } else if (isDownVoted) {
      setDownVotes((state) => {
        return state - 1;
      });
      setDownVote(false);
      RemoveDownVote(props.id, localStorage.getItem("AnonymityToken#123"));
    }
  };
  const deletePost = async () => {
    const headers = new Headers();
    const token = localStorage.getItem("AnonymityToken#123");
    headers.append("Authorization", token);
    headers.append("content-type", "application/json");
    const request = new Request(`${url}/user/post/${props.id}`, {
      method: "DELETE",
      headers,
    });
    try {
      const response = await fetch(request);
      if (response.status === 200) {
        window.location.reload(true);
      }
      setError(true);
    } catch (e) {
      setError(true);
    }
  };
  const [isImageLoading, setImage] = useState(true);
  const [isError, setError] = useState(false);
  const [showImageModal, setModal] = useState(false);
  const [showDeleteModal, setDeleteModal] = useState(false);
  const calculateWidth = () => {
    if (props.deviceType === "comp") {
      return { width: 800 };
    }
    if (props.deviceType === "mob") {
      return { width: 350 };
    }
    if (props.deviceType === "tab") {
      return { width: 600 };
    }
    if (!props.deviceType) {
      return { width: "100%" };
    }
  };

  return (
    <Container fluid>
      <Card
        style={{
          ...calculateWidth(),
          border: "none",
          display: "flex",
          justifyContent: "center",
          borderRadius: 0,
          marginBottom: 10,
        }}
      >
        <Card.Content>
          <Image
            src={props.profilePhoto}
            floated="left"
            size="mini"
            rounded
          ></Image>
          <Card.Header style={{ color: "grey", fontFamily: "poppins" }}>
            {props.username}
          </Card.Header>
          <Card.Meta style={{ color: "grey", fontFamily: "poppins" }}>
            {"Posted " +
              moment(props.createdAt).fromNow() +
              " \n" +
              "On " +
              moment(props.createdAt).format("LL")}
          </Card.Meta>
        </Card.Content>
        {isImageLoading ? (
          <Placeholder>
            <PlaceholderImage rectangular></PlaceholderImage>
          </Placeholder>
        ) : (
          ""
        )}
        <div
          onClick={() => {
            setModal(true);
          }}
        >
          <Image
            src={props.image}
            style={
              isImageLoading || isError
                ? { width: 0, height: 0 }
                : { width: "100%" }
            }
            onLoad={() => {
              setImage(false);
            }}
            onError={() => {
              setImage(false);
              setError(true);
            }}
          ></Image>
        </div>
        <Card.Content>
          <Card.Content>
            <div
              style={{
                fontFamily: "poppins",
                fontSize: 12,
                fontWeight: "bolder",
                color: "black",
              }}
            >
              Problem Statement
            </div>
          </Card.Content>
          <Card.Content>
            <Card.Description
              style={{
                fontWeight: "500",
                fontFamily: "poppins",
                wordWrap: "break-word",
                hyphens: "auto",
                marginTop: 10,
              }}
            >
              {props.description}
            </Card.Description>
          </Card.Content>
        </Card.Content>

        <Card.Content extra>
          {upVotes}
          <IconButton
            size="medium"
            style={{
              marginLeft: "0.5%",
              marginRight: "1%",
              color: isUpVoted ? blue[700] : "grey",
            }}
            onClick={() => {
              HandleUpVote();
            }}
          >
            <ArrowUpward></ArrowUpward>
          </IconButton>
          {downVotes}
          <IconButton
            size="medium"
            style={{
              marginLeft: "1%",
              color: isDownVoted ? red[500] : "grey",
            }}
            onClick={() => {
              handleDownVote();
            }}
          >
            <ArrowDownward></ArrowDownward>
          </IconButton>
          <IconButton
            onClick={() => {
              handleGetComments();
              setCommentSection(true);
            }}
            size="small"
          >
            <CommentOutlined></CommentOutlined>
          </IconButton>
          {props.hasDeleteButton ? (
            <IconButton
              onClick={() => {
                setDeleteModal(true);
              }}
              style={{ float: "right" }}
            >
              <Delete style={{ color: "red" }}></Delete>
            </IconButton>
          ) : (
            ""
          )}
        </Card.Content>
        <Card.Content>
          {commentSectionLoading ? (
            <CircularProgress></CircularProgress>
          ) : (
            <input
              placeholder="Comment..."
              value={commentDescription}
              onChange={(e) => {
                setCommentDescription(e.target.value);
              }}
              style={{
                width: "80%",
                border: "none",
                fontSize: 14,
                fontFamily: "poppins",
                padding: 4,
                outline: "none",
              }}
            ></input>
          )}
          <button
            disabled={commentDescription.length > 0 ? false : true}
            onClick={async () => {
              try {
                const token = localStorage.getItem("AnonymityToken#123");
                setCommentSectionLoading(true);
                const res = await postComment(
                  props.id,
                  token,
                  commentDescription
                );
                const bdy = await res.json();
                if (res.status === 200) {
                  setComments(
                    comments.concat([
                      {
                        description: commentDescription,
                        By: {
                          profilephoto: props.profilePhoto1,
                          firstName: "By",
                          LastName: "Me... Right Now",
                        },
                      },
                    ])
                  );
                  setCommentDescription("");
                }
                setCommentSectionLoading(false);
              } catch (e) {
                setCommentSectionLoading(false);
              }
            }}
            style={{
              padding: 4,
              color: commentDescription.length == 0 ? blue[100] : blue[500],
              float: "right",
              background: "transparent",
              fontSize: 15,
              fontFamily: "poppins",
              border: "none",
              outline: "none",
              cursor: "pointer",
              marginRight: 20,
            }}
          >
            Post
          </button>
        </Card.Content>
      </Card>
      <Divider></Divider>
      <Modal
        open={showImageModal}
        basic
        onClose={() => {
          setModal(false);
        }}
        closeIcon
      >
        <Image src={props.image} centered></Image>
      </Modal>
      {props.hasDeleteButton ? (
        <Modal basic open={showDeleteModal}>
          <Modal.Header>Delete Your Problem</Modal.Header>
          <Modal.Content>
            <p>Are you sure you want to delete This Post ??</p>
          </Modal.Content>
          <Modal.Actions>
            <Button
              onClick={() => {
                setDeleteModal(false);
              }}
              color="red"
              inverted
            >
              No
            </Button>
            <Button
              positive
              onClick={() => {
                deletePost();
              }}
              color="green"
              inverted
            >
              Yes
            </Button>
          </Modal.Actions>
        </Modal>
      ) : (
        ""
      )}
      <Modal
        open={commentSection}
        onClose={() => {
          setCommentSection(false);
        }}
      >
        <ModalHeader
          style={{ fontFamily: "poppins", backgroundColor: "rgba(0,0,0,1)" }}
        >
          <div
            style={{
              display: "flex",
              justifyContent: "flex-start",
              color: "rgba(255,255,255,0.5)",
            }}
          >
            Comments
          </div>
        </ModalHeader>
        <Modal.Content scrolling>
          <Comment.Group>
            {comments.length > 0 && !commentsFetching ? (
              comments.map((comment, index) => {
                return (
                  <Comment>
                    <Comment.Avatar
                      src={
                        comments[comments.length - index - 1].By.profilephoto
                      }
                    />
                    <Comment.Content>
                      <Comment.Author
                        as="a"
                        style={{
                          fontFamily: "poppins",
                        }}
                      >
                        {comments[comments.length - index - 1].By.firstName +
                          " " +
                          comments[comments.length - index - 1].By.LastName}
                      </Comment.Author>

                      <Comment.Text
                        style={{
                          fontFamily: "poppins",
                        }}
                      >
                        {comments[comments.length - index - 1].description}
                      </Comment.Text>
                      <Comment.Actions>
                        <Comment.Action>Like</Comment.Action>
                      </Comment.Actions>
                    </Comment.Content>
                  </Comment>
                );
              })
            ) : commentsFetching ? (
              <Placeholder>
                <Placeholder.Header image>
                  <Placeholder.Line></Placeholder.Line>
                  <Placeholder.Line></Placeholder.Line>
                </Placeholder.Header>
                <Placeholder.Header image>
                  <Placeholder.Line></Placeholder.Line>
                  <Placeholder.Line></Placeholder.Line>
                </Placeholder.Header>
                <Placeholder.Header image>
                  <Placeholder.Line></Placeholder.Line>
                  <Placeholder.Line></Placeholder.Line>
                </Placeholder.Header>
              </Placeholder>
            ) : (
              <Result
                status="404"
                title="No Comments !!!"
                subTitle="Wanna Put Some???"
              ></Result>
            )}
          </Comment.Group>
        </Modal.Content>
        <Divider style={{ marginBottom: 0, marginTop: 0 }}></Divider>
        <Modal.Content>
          {commentSectionLoading ? (
            <CircularProgress></CircularProgress>
          ) : (
            <input
              placeholder="Comment..."
              value={commentDescription}
              onChange={(e) => {
                setCommentDescription(e.target.value);
              }}
              style={{
                width: "80%",
                border: "none",
                fontSize: 14,
                fontFamily: "poppins",
                padding: 4,
                outline: "none",
              }}
            ></input>
          )}
          <button
            disabled={commentDescription.length > 0 ? false : true}
            onClick={async () => {
              try {
                const token = localStorage.getItem("AnonymityToken#123");
                setCommentSectionLoading(true);
                const res = await postComment(
                  props.id,
                  token,
                  commentDescription
                );
                const bdy = await res.json();
                if (res.status === 200) {
                  setComments(
                    comments.concat([
                      {
                        description: commentDescription,
                        By: {
                          profilephoto: props.profilePhoto1,
                          firstName: "By",
                          LastName: "Me... Right Now",
                        },
                      },
                    ])
                  );
                  setCommentDescription("");
                }
                setCommentSectionLoading(false);
              } catch (e) {
                setCommentSectionLoading(false);
              }
            }}
            style={{
              padding: 4,
              color: commentDescription.length == 0 ? blue[100] : blue[500],
              float: "right",
              background: "transparent",
              fontSize: 15,
              fontFamily: "poppins",
              border: "none",
              outline: "none",
              cursor: "pointer",
              marginRight: 20,
            }}
          >
            Post
          </button>
        </Modal.Content>
      </Modal>
    </Container>
  );
};
const mapStateToProps = (state) => {
  return {
    profilePhoto1: state.userReducer.profilePhoto,
  };
};

export default connect(mapStateToProps)(Problem);
