import React from "react";
import { connect } from "react-redux";
import {
  BottomNavigation,
  AppBar,
  Toolbar,
  Typography,
  createMuiTheme,
  ThemeProvider,
  Slide,
  Button,
  BottomNavigationAction,
  IconButton
} from "@material-ui/core";

import { Image, Visibility } from "semantic-ui-react";
import {
  Home,
  Search,
  NearMe,
  Info,
  ListAltOutlined as List
} from "@material-ui/icons";

import { BackTop } from "antd";
// const myWhite = "rgba(255,255,255,0.9)";
const myBlack = "rgba(0,0,0,0.5)";
const myTheme = createMuiTheme({
  palette: {
    primary: {
      main: "rgba(0,0,0,0.0)"
    },
    secondary: {
      main: myBlack
    }
  }
});

const bottomTheme = createMuiTheme({
  palette: {
    primary: {
      main: "rgba(0,0,0,1)"
    }
  }
});

const ForMobile = props => {
  const [isTop, setDirection] = React.useState(true);
  const [topVisible, setTop] = React.useState(true);
  return (
    <Visibility
      onUpdate={(e, { calculations }) => {
        setTop(calculations.topVisible);
        setDirection(calculations.direction === "up");
      }}
    >
      <BackTop
        style={{ position: "fixed", right: "2%", bottom: "10%" }}
      ></BackTop>
      <ThemeProvider theme={myTheme}>
        <Slide in={isTop || topVisible}>
          <AppBar color="primary" position="static" elevation={0}>
            <Toolbar style={{ height: 10 }}>
              <Button
                style={{ textTransform: "none" }}
                onClick={() => {
                  props.dispatch({
                    type: "Home"
                  });
                  props.history.push("/anonymity/Home");
                }}
              >
                <Image
                  src={require("../../../images/complainify.png")}
                  size="mini"
                ></Image>
                <Typography
                  variant="h6"
                  style={{ color: "black", marginLeft: "10%" }}
                >
                  {props.currentTab}
                </Typography>
              </Button>
              <div style={{ flexGrow: 1 }}></div>

              <IconButton>
                <Search></Search>
              </IconButton>
            </Toolbar>
          </AppBar>
        </Slide>
      </ThemeProvider>
      <ThemeProvider theme={bottomTheme}>
        <BottomNavigation
          value={props.currentTab}
          onChange={(event, type) => {
            props.dispatch({
              type
            });
            props.history.push("/anonymity/" + type);
          }}
          style={{ position: "fixed", bottom: 0, zIndex: 100, width: "100%" }}
        >
          <BottomNavigationAction
            // label="Home"
            value={"Home"}
            icon={
              <Home
                style={{
                  color: props.currentTab === "Home" ? "orange" : "grey"
                }}
              ></Home>
            }
          ></BottomNavigationAction>
          <BottomNavigationAction
            // label="NearBy"
            value={"Nearby"}
            icon={
              <NearMe
                style={{
                  color: props.currentTab === "Nearby" ? "orange" : "grey"
                }}
              ></NearMe>
            }
          ></BottomNavigationAction>
          <BottomNavigationAction
            value={"Ranking"}
            icon={
              <List
                style={{
                  color: props.currentTab === "Ranking" ? "orange" : "grey"
                }}
              ></List>
            }
          ></BottomNavigationAction>
          <BottomNavigationAction
            // label="About"
            value={"About"}
            icon={
              <Info
                style={{
                  color: props.currentTab === "About" ? "orange" : "grey"
                }}
              ></Info>
            }
          ></BottomNavigationAction>
        </BottomNavigation>
      </ThemeProvider>
    </Visibility>
  );
};

const mapStateToProps = state => {
  return {
    currentTab: state.navigationReducer.currentTab
  };
};

export default connect(mapStateToProps)(ForMobile);
