import React from "react";
import { connect } from "react-redux";
import {
  Tabs,
  Tab,
  Paper,
  createMuiTheme,
  ThemeProvider,
} from "@material-ui/core";
import { orange, blue } from "@material-ui/core/colors";
import {
  NotificationsActiveOutlined,
  ChatBubbleOutlineOutlined,
  SentimentDissatisfiedOutlined,
  NotesOutlined,
} from "@material-ui/icons";
// import { BackTop } from "antd";
const theme = createMuiTheme({
  palette: {
    primary: blue,
    secondary: orange,
  },
});
class ForComp extends React.Component {
  componentDidMount() {
    if (this.props.location.pathname.toLowerCase().includes("notes"))
      this.props.dispatch({ type: "Notes" });
    else if (this.props.location.pathname.toLowerCase().includes("myproblems"))
      this.props.dispatch({ type: "MyProblems" });
    else if (this.props.location.pathname.toLowerCase().includes("notices"))
      this.props.dispatch({ type: "Notices" });
    else {
      this.props.dispatch({ type: "MyProblems" });
      this.props.history.push("/anonymity/home/MyProblems");
    }
  }
  render() {
    return (
      <ThemeProvider theme={theme}>
        <Paper style={{ backgroundColor: "rgba(0,0,0,0.01)" }} elevation={0}>
          <Tabs
            onChange={(event, value) => {
              if (value == 2) {
                this.props.history.push("/anonymity/Home/Notes");
                this.props.dispatch({ type: "Notes" });
              } else if (value == 0) {
                this.props.history.push("/anonymity/Home/MyProblems");
                this.props.dispatch({ type: "MyProblems" });
              } else if (value == 1) {
                this.props.history.push("/anonymity/Home/Notices");
                this.props.dispatch({ type: "Notices" });
              }
            }}
            // textColor="secondary"

            centered
            value={this.props.currentHomeTab}
          >
            <Tab
              // style={{ marginRight: "2%" }}
              color="primary"
              textColor="primary"
              icon={
                <SentimentDissatisfiedOutlined></SentimentDissatisfiedOutlined>
              }
              label="My Problems"
            ></Tab>
            <Tab
              icon={<NotificationsActiveOutlined></NotificationsActiveOutlined>}
              label="Notices"
            ></Tab>
            <Tab
              // style={{ marginRight: "2%" }}
              icon={<NotesOutlined></NotesOutlined>}
              label="Notes"
            ></Tab>
          </Tabs>
        </Paper>
      </ThemeProvider>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    currentHomeTab: state.HomeReducer,
  };
};

export default connect(mapStateToProps)(ForComp);
