import React from "react";
import { BrowserRouter, Route, Switch } from "react-router-dom";
import AuthHeader from "../authHeader/Header";
import "../../animation.css";
import Login from "../Login/Login";
import { connect } from "react-redux";
import Signup from "../SignUp/signup";
import HomeNav from "../HomeNavigator/homenav";
// import NearBy from "../NearBy/Problems/problems";
import NotFound from "../NotFound/Not";
import Auth from "../AuthenticateComponent/Auth";
import MyProblems from "../Home/MyProblems/MyProblems";
import NearByProblems from "../NearBy/Problems/problems";
const AuthMyProblems = (props) => {
  return <Auth {...props} Component={MyProblems}></Auth>;
};
const AuthNearByProblems = (props) => {
  return <Auth {...props} Component={NearByProblems}></Auth>;
};

const Router = () => {
  const AuthenticatedRoutes = (
    <div>
      <Route path="/anonymity" component={AuthHeader}></Route>
      <Route path="/anonymity/Home" component={HomeNav}></Route>
      <Switch>
        <Route path="/anonymity/Nearby" component={AuthNearByProblems}></Route>
        <Route exact path="/" component={Login}></Route>
        <Route exact path="/signup" component={Signup}></Route>
        <Route exact path="/loginA" component={Login}></Route>
        <Route exact path="/login" component={Login}></Route>
        <Route
          exact
          path="/anonymity/Home/MyProblems"
          component={AuthMyProblems}
        ></Route>
        <Route path="" component={NotFound}></Route>
      </Switch>
    </div>
  );

  return <BrowserRouter>{AuthenticatedRoutes}</BrowserRouter>;
};

const mapStateToProps = (state) => {
  return {
    isAuth: state.authReducer,
  };
};

export default connect(mapStateToProps)(Router);
