import React from "react";
import { connect } from "react-redux";
import {
  AppBar,
  createMuiTheme,
  ThemeProvider,
  Typography,
  Toolbar,
  Button,
  Slide,
  IconButton
} from "@material-ui/core";
import { Divider, Search } from "semantic-ui-react";
import "../styles/lift.css";
import {
  setAbout,
  setRanking,
  setHome,
  setNearBy
} from "../../../redux/action";
import { Image } from "semantic-ui-react";
import { orange } from "@material-ui/core/colors";
import {
  Ranking,
  NearBy,
  Home as home,
  About
} from "../../../redux/actionTypes";
import "./style.css";
import { Person, VerticalAlignTopOutlined } from "@material-ui/icons";
import { BackTop } from "antd";
const myOrange = "rgba(255,165,0,1)";

const theme1 = createMuiTheme({
  palette: {
    secondary: {
      main: "rgba(255,255,255,1)"
    },
    primary: orange
  }
});

const ForComputer = props => {
  return (
    <ThemeProvider theme={theme1}>
      <BackTop>
        <IconButton
          style={{
            background: "rgba(0,0,0,0.5)",
            color: "white",
            boxShadow: "1px 1px 15px grey"
          }}
        >
          <VerticalAlignTopOutlined></VerticalAlignTopOutlined>
        </IconButton>
      </BackTop>
      <AppBar color="secondary" elevation={0} position="static">
        <Toolbar
          style={{
            marginLeft: "8%",
            marginRight: "1%"
          }}
        >
          <Image
            src={require("../../../images/complainify.png")}
            size="tiny"
            style={{ marginRight: 5 }}
          ></Image>
          <Typography
            style={{
              color: "rgba(0,0,0,0.6)",
              float: "left",
              fontWeight: "bold",
              marginRight: "5%"
              // flexGrow: 1
            }}
            variant="h6"
          >
            Anonymity
          </Typography>
          <div style={{ display: "flex", justifyContent: "space-between" }}>
            <Button
              size="large"
              onClick={() => {
                props.setHome();
                props.history.push("/anonymity/" + home);
              }}
              style={{
                marginRight: 10,
                textTransform: "none",
                transition: "all 0.15s",
                fontFamily: "poppins",

                borderBottom:
                  props.currentTab === "Home"
                    ? "2px solid black"
                    : "2px solid white",
                borderRadius: "0px",
                transform:
                  props.currentTab === "Home"
                    ? "translateY(-5px)"
                    : "translateY(0px)",
                fontWeight: props.currentTab === "Home" ? "bold" : ""
              }}
            >
              Home
            </Button>
            <Button
              disableElevation
              onClick={() => {
                props.setNearBy();
                props.history.push("/anonymity/" + NearBy);
              }}
              size="large"
              style={{
                marginRight: 10,
                textTransform: "none",
                transition: "all 0.15s",

                fontFamily: "poppins",
                borderBottom:
                  props.currentTab === "Nearby"
                    ? "2px solid black"
                    : "2px solid white",
                borderRadius: "0px",
                transform:
                  props.currentTab === "Nearby"
                    ? "translateY(-5px)"
                    : "translateY(0px)",
                fontWeight: props.currentTab === "Nearby" ? "bold" : ""
              }}
            >
              Nearby
            </Button>
            <Button
              size="large"
              style={{
                marginRight: 10,
                textTransform: "none",
                transition: "all 0.15s",

                fontFamily: "poppins",
                borderBottom:
                  props.currentTab === "Ranking"
                    ? "2px solid black"
                    : "2px solid white",
                borderRadius: "0px",
                transform:
                  props.currentTab === "Ranking"
                    ? "translateY(-5px)"
                    : "translateY(0px)",
                fontWeight: props.currentTab === "Ranking" ? "bold" : ""
              }}
              onClick={() => {
                props.setRanking();
                props.history.push("/anonymity/" + Ranking);
              }}
            >
              Ranking
            </Button>
            <Button
              size="large"
              onClick={() => {
                props.setAbout();
                props.history.push("/anonymity/" + About);
              }}
              style={{
                marginRight: 10,
                transition: "all 0.15s",

                textTransform: "none",
                fontFamily: "poppins",
                transform:
                  props.currentTab === "About"
                    ? "translateY(-5px)"
                    : "translateY(0px)",
                borderBottom:
                  props.currentTab === "About"
                    ? "2px solid black"
                    : "2px solid white",
                borderRadius: "0px",
                fontWeight: props.currentTab === "About" ? "bold" : ""
              }}
            >
              About
            </Button>
          </div>
          <div style={{ flexGrow: 1 }}></div>
          {props.currentTab != "Adult" ? <Search loading={false}></Search> : ""}
          <Button
            size="large"
            style={{
              margin: 10,
              color: "grey",
              fontWeight: "bold",
              textTransform: "none",
              fontFamily: "poppins"
            }}
          >
            <Image
              size="mini"
              style={{ marginRight: 10 }}
              src={
                props.profilePhoto
                // "https://steamuserimages-a.akamaihd.net/ugc/785228829883410301/B0FB3F1FF96596F9528A9D7C1102C26E791256A8/"
              }
              circular
            ></Image>
            {props.username}
          </Button>
        </Toolbar>
      </AppBar>
      <Divider style={{ marginTop: 0, marginBottom: 0 }}></Divider>
    </ThemeProvider>
  );
};
// <Person style={{ marginRight: 10, color: "orange" }}></Person>

// const mapStateToProps = state => {
//   const { navigationReducer } = state;
//   return {
//     currentTab: navigationReducer.currentTab
//   };
// };

const mapStateToProps = state => {
  return {
    currentTab: state.navigationReducer.currentTab,
    width: state.visibility.Width,
    username: state.userReducer.username
  };
};

export default connect(mapStateToProps, {
  setAbout,
  setRanking,
  setHome,
  setNearBy
})(ForComputer);
