import React from "react";
import validator from "validator";
import {
  TextField,
  createMuiTheme,
  ThemeProvider,
  InputAdornment,
  IconButton,
  Container,
  Button,
  CircularProgress,
  Backdrop,
  Snackbar
} from "@material-ui/core";
import AlreadyLoggedIn from "./alreadyLoggedIn";
import Alert from "@material-ui/lab/Alert";
import url from "../../strings/url";
import Check from "@material-ui/icons/Check";
import Cross from "@material-ui/icons/CloseOutlined";
import Visibility from "@material-ui/icons/Visibility";
import VisibilityOff from "@material-ui/icons/VisibilityOff";
import Header from "../NotAuthHeader/Header";
import { Divider } from "semantic-ui-react";
import { blue, orange, green } from "@material-ui/core/colors";
const theme = createMuiTheme({
  palette: {
    primary: {
      main: orange[600]
    },
    secondary: {
      main: green[500]
    }
  }
});
const theme1 = createMuiTheme({
  palette: {
    primary: {
      main: orange[300]
    },
    secondary: {
      main: blue[600]
    }
  }
});

export default class Login extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      email: "",
      password: "",
      showPass: false,
      isEmailRight: "unknown",
      isFetchingEmail: false,
      isPasswordCorrect: "unknown",
      LoadingAfterSignUp: false,
      LogInFailed: false,
      isLoggedIn: "unknown",
      username: ""
    };
    this.handleChange = this.handleChange.bind(this);
    this.showPass = this.showPass.bind(this);
    this.onBlur = this.onBlur.bind(this);
    this.emptyEmail = this.emptyEmail.bind(this);
    this.onSignInHandle = this.onSignInHandle.bind(this);
    this.handleSnackClose = this.handleSnackClose.bind(this);
    this.makeFalseIsLogin = this.makeFalseIsLogin.bind(this);
  }
  componentDidMount() {
    const token = localStorage.getItem("AnonymityToken#123");
    if (token) {
      const headers = new Headers();
      headers.append("Authorization", token);
      headers.append("content-type", "application/json");
      const request = new Request(`${url}/user/isAuth`, {
        method: "get",
        headers
      });
      fetch(request)
        .then(async res => {
          if (res.status == 200) {
            const { username } = await res.json();
            this.setState({ isLoggedIn: true, username });
          } else if (res.status == 401) {
            this.setState({ isLoggedIn: false });
          }
        })
        .catch(e => {
          this.setState({ isLoggedIn: false });
        });
    } else {
      this.setState({ isLoggedIn: false });
    }
  }
  handleChange(e) {
    this.setState({ LogInFailed: false });
    if (e.target.name === "email") {
      this.setState({ isEmailRight: "unknown" });
    }
    if (e.target.name === "password") {
      if (e.target.value.length > 0) {
        this.setState({
          isPasswordCorrect: e.target.value.length >= 8
        });
      } else {
        this.setState({ isPasswordCorrect: "unknown" });
      }
    }
    this.setState({ [e.target.name]: e.target.value });
  }
  showPass(e) {
    this.setState(state => ({ showPass: !state.showPass }));
  }
  onBlur(e) {
    if (e.target.name === "email") {
      if (validator.default.isEmail(this.state.email)) {
        const headers = new Headers();
        headers.append("content-type", "application/json");
        const request = new Request(
          `${url}/user/verifyEmail/${this.state.email}`,
          {
            method: "GET",
            headers
          }
        );
        this.setState({ isFetchingEmail: true });
        fetch(request)
          .then(async res => {
            try {
              const response = await res.json();
              this.setState({
                isFetchingEmail: false,
                isEmailRight: !response.isEmailValid
              });
            } catch (e) {
              this.setState({ isFetchingEmail: false });
            }
          })
          .catch(e => {
            this.setState({ isFetchingEmail: false });
          });
      } else if (this.state.email) {
        this.setState({ isEmailRight: false });
      }
    }
  }
  makeFalseIsLogin(e) {
    this.setState({ isLoggedIn: false });
  }
  emptyEmail(e) {
    this.setState({ email: "", isEmailRight: "unknown" });
  }
  onSignInHandle() {
    const headers = new Headers();
    let body = { email: this.state.email, password: this.state.password };
    body = JSON.stringify(body);
    headers.append("content-type", "application/json");
    const request = new Request(`${url}/user/login`, {
      method: "POST",
      body,
      headers
    });
    this.setState({ LoadingAfterSignUp: true });
    fetch(request)
      .then(async res => {
        if (res.status === 200) {
          const { token } = await res.json();
          localStorage.setItem("AnonymityToken#123", token);
          return this.props.history.push("/anonymity/home");
        }
        if (res.status === 401)
          this.setState({
            LoadingAfterSignUp: false,
            LogInFailed: "Wrong Credentials"
          });
      })
      .catch(e => {
        console.log("Error");
        this.setState({
          LoadingAfterSignUp: false,
          LogInFailed: "Connection Error"
        });
      });
  }
  handleSnackClose() {
    this.setState({ LogInFailed: false });
  }
  render() {
    return (
      <div>
        <Backdrop
          in={this.state.isLoggedIn == "unknown"}
          open={this.state.isLoggedIn == "unknown"}
          style={{ zIndex: 101 }}
        >
          <CircularProgress
            style={{ color: "white", width: 150, height: 150 }}
          ></CircularProgress>
        </Backdrop>

        {!this.state.isLoggedIn ? (
          <div>
            <Snackbar
              anchorOrigin={{ vertical: "top", horizontal: "center" }}
              open={!!this.state.LogInFailed}
              autoHideDuration={6000}
              message={this.state.LogInFailed}
              onClose={this.handleSnackClose}
            >
              <Alert severity="error">{this.state.LogInFailed}</Alert>
            </Snackbar>
            <Container maxWidth="xs">
              <Header heading="Anonymity Login" divider={true}></Header>
              <ThemeProvider theme={theme}>
                <TextField
                  color={
                    this.state.isEmailRight === true ? "secondary" : "primary"
                  }
                  error={!this.state.isEmailRight}
                  className="comingFromLeft"
                  name="email"
                  label="Email"
                  variant="filled"
                  FormHelperTextProps={{ className: "comingFromLeft" }}
                  helperText={
                    this.state.isEmailRight === false ? "Email Not Valid" : ""
                  }
                  value={this.state.email}
                  onChange={this.handleChange}
                  onBlur={this.onBlur}
                  InputProps={{
                    style: {
                      backgroundColor: "rgba(0,0,0,0.02)",
                      fontWeight: "bold",
                      color: "grey"
                    },
                    endAdornment: (
                      <InputAdornment>
                        {this.state.isFetchingEmail ? (
                          <CircularProgress
                            style={{ width: 20, height: 20 }}
                          ></CircularProgress>
                        ) : (
                          ""
                        )}
                        {this.state.isEmailRight === true ? (
                          <IconButton>
                            <Check style={{ color: green[500] }}></Check>
                          </IconButton>
                        ) : (
                          ""
                        )}
                        {this.state.isEmailRight === false ? (
                          <IconButton>
                            <Cross onClick={this.emptyEmail}></Cross>
                          </IconButton>
                        ) : (
                          ""
                        )}
                      </InputAdornment>
                    )
                  }}
                  size="medium"
                  style={{
                    width: "100%",
                    fontWeight: "bolder"
                  }}
                ></TextField>

                <TextField
                  color={
                    this.state.isPasswordCorrect === true
                      ? "secondary"
                      : "primary"
                  }
                  error={!this.state.isPasswordCorrect}
                  className="comingFromRight"
                  name="password"
                  label="password"
                  FormHelperTextProps={{ className: "comingFromRight" }}
                  size="medium"
                  type={this.state.showPass ? "text" : "password"}
                  variant="filled"
                  helperText={
                    this.state.isPasswordCorrect === false
                      ? "Password Length Must Be Greater Than 7"
                      : ""
                  }
                  value={this.state.password}
                  onChange={this.handleChange}
                  style={{
                    width: "100%",
                    fontWeight: "bolder",
                    marginTop: 30
                  }}
                  InputProps={{
                    endAdornment: (
                      <InputAdornment position="end">
                        <IconButton onClick={this.showPass}>
                          {this.state.showPass ? (
                            <Visibility></Visibility>
                          ) : (
                            <VisibilityOff></VisibilityOff>
                          )}
                        </IconButton>
                      </InputAdornment>
                    ),
                    style: {
                      backgroundColor: "rgba(0,0,0,0.02)",
                      fontWeight: "bold",
                      color: "grey"
                    }
                  }}
                ></TextField>
              </ThemeProvider>
              <ThemeProvider theme={theme1}>
                <Button
                  color="primary"
                  size="large"
                  style={{
                    width: "100%",
                    marginTop: 100
                  }}
                  onClick={this.onSignInHandle}
                  variant="contained"
                  disabled={
                    this.state.isEmailRight === true &&
                    this.state.isPasswordCorrect === true
                      ? false
                      : true
                  }
                  disableElevation
                  className="comingFromDown"
                >
                  Sign In
                </Button>
                <Divider horizontal style={{ color: "grey" }}>
                  OR
                </Divider>
                <Button
                  color="secondary"
                  size="large"
                  style={{
                    width: "100%",
                    textTransform: "lowercase",
                    color: "grey"
                  }}
                  onClick={() => {
                    return this.props.history.push("/signup");
                  }}
                >
                  Don't Have a Account ? Sign Up
                </Button>
              </ThemeProvider>

              <Backdrop
                open={this.state.LoadingAfterSignUp}
                style={{ zIndex: 120 }}
              >
                <CircularProgress style={{ color: "white" }}></CircularProgress>
              </Backdrop>
            </Container>
          </div>
        ) : (
          ""
        )}
        {this.state.isLoggedIn == true ? (
          <AlreadyLoggedIn
            makeFalse={this.makeFalseIsLogin}
            username={this.state.username}
            history={this.props.history}
          ></AlreadyLoggedIn>
        ) : (
          ""
        )}
      </div>
    );
  }
}
