import React, { useState, useEffect } from "react";
import { Responsive } from "semantic-ui-react";
import ForComp from "./forComp/forComp";
import ForTab from "./forTablet/forTab";
import ForMob from "./forMobile/forMob";
import { getNearByProblems } from "../../../ApiRequests/request";

import { connect } from "react-redux";
const Problems = (props) => {
  const [isError, setError] = useState(false);
  const [isLoading, setLoading] = useState(false);
  const [pageNo, setPageNo] = useState(1);
  const [Problems, setProblems] = useState([]);
  const [requestMade, setRequestMade] = useState(0);
  const [totalProblems, setTotal] = useState(0);
  const getProblems = async (pageNo) => {
    try {
      setLoading(true);
      const response = await getNearByProblems(props.token, (pageNo - 1) * 10);
      if (response.ok) {
        const { problems, count } = await response.json();
        if (problems) {
          setProblems(problems);
          setTotal(count);
          setLoading(false);
          setRequestMade(1);
        }
        return;
      }
      setLoading(false);
      setError(true);
      setRequestMade(1);
    } catch (e) {
      setLoading(false);
      setError(true);
      setRequestMade(1);
      console.log(e.message);
    }
  };
  useEffect(() => {
    if (requestMade === 0) getProblems(pageNo);
  });

  return (
    <div>
      <Responsive {...Responsive.onlyComputer}>
        <ForComp
          deviceType={"comp"}
          Problems={Problems}
          id={props.id}
          pageNo={pageNo}
          setPageNo={(pageNo) => {
            getProblems(pageNo);
            setPageNo(pageNo);
          }}
          problemsCount={totalProblems}
          isError={isError}
          isLoading={isLoading}
        ></ForComp>
      </Responsive>
      <Responsive {...Responsive.onlyTablet}>
        <ForTab
          deviceType={"tab"}
          Problems={Problems}
          id={props.id}
          pageNo={pageNo}
          setPageNo={(pageNo) => {
            getProblems(pageNo);
            setPageNo(pageNo);
          }}
          problemsCount={totalProblems}
          isError={isError}
          isLoading={isLoading}
        ></ForTab>
      </Responsive>
      <Responsive {...Responsive.onlyMobile}>
        <ForMob
          deviceType={"mob"}
          Problems={Problems}
          id={props.id}
          pageNo={pageNo}
          setPageNo={(pageNo) => {
            getProblems(pageNo);
            setPageNo(pageNo);
          }}
          problemsCount={totalProblems}
          isError={isError}
          isLoading={isLoading}
        ></ForMob>
      </Responsive>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    token: state.userReducer.token,
    id: state.userReducer.id,
  };
};

export default connect(mapStateToProps)(Problems);
