import url from "../strings/url";

export const isAuthRequest = (token) => {
  const headers = new Headers();
  headers.append("content-type", "application/json");
  headers.append("Authorization", token);
  const request = new Request(`${url}/user/isAuth`, { method: "GET", headers });
  return fetch(request);
};

export const getMyProblems = (token, start, offset, sortBy) => {
  const headers = new Headers();
  headers.append("content-type", "application/json");
  headers.append("Authorization", token);
  const request = new Request(
    `${url}/user/myProblems/${start}/${offset}?sortBy=${
      sortBy !== "Latest" ? sortBy : ""
    }`,
    {
      method: "GET",
      headers,
    }
  );
  console.log(sortBy);
  return fetch(request);
};

export const getNearByProblems = (token, skip) => {
  const headers = new Headers();
  headers.append("Authorization", token);
  const request = new Request(`${url}/user/getProblems/${skip}`, {
    method: "GET",
    headers,
  });
  return fetch(request);
};

export const postMyPost = (
  token,
  Description,
  type,
  Media,
  Anonymous = false
) => {
  const headers = new Headers();
  const body = JSON.stringify({
    Description,
    Post_Type: type,
    Media,
    Anonymous,
  });

  headers.append("content-type", "application/json");
  headers.append("Authorization", token);
  const request = new Request(`${url}/user/post`, {
    method: "POST",
    headers,
    body,
  });

  return fetch(request);
};

